import { IFetchClient } from './IFetchClient';
import { IFetchOptions } from './IFetchOptions';
import { IFetchResponse } from './IFetchResponse';
import { IIdentified } from './IIdentified';
import { IResult } from './IResult';
import { IResultList } from './IResultList';
import { Paging } from './Paging';

import { Realtime } from '../realtime/index';

export abstract class Service<TData extends IIdentified> {
  protected propertyName: string;
  protected abstract baseUrl: string;
  protected abstract listUrl: string;
  protected channel: string;
  private cache = new Map();

  constructor(protected client: IFetchClient, protected realtime?: Realtime) {
    const methodsToHide = ['list', 'create', 'update', 'detail', 'delete'];
    const prototype = Object.getPrototypeOf(this);
    methodsToHide.forEach(method => {
      if (!prototype.hasOwnProperty(method)) {
        Object.defineProperty(this, method, {
          get() {
            return undefined;
          }
        });
      }
    });
  }

  async list(filter: object = {}): Promise<IResultList<TData>> {
    const headers = { accept: 'application/json' };
    const url = this.listUrl;
    const res = await this.fetch(url, this.changeFetchOptions({ headers, params: filter }, url));
    const json = await res.json();
    const data = this.propertyName ? json[this.propertyName] : json;
    const paging = this.getPaging(json, filter);
    return { res, data, paging };
  }

  protected async detail(
    entityOrId: string | number | IIdentified,
    filter: object = {}
  ): Promise<IResult<TData>> {
    const headers = { accept: 'application/json' };
    const url = this.getDetailUrl(entityOrId);
    const res = await this.fetch(
      url,
      this.changeFetchOptions({ headers, params: { ...filter } }, url)
    );
    const data = await res.json();
    return { res, data };
  }

  protected async create(entity: Partial<TData>): Promise<IResult<TData>> {
    const url = this.listUrl;
    const method = 'POST';
    const body = JSON.stringify(this.onBeforeCreate(entity));
    const headers = { 'content-type': 'application/json', accept: 'application/json' };
    const res = await this.fetch(url, this.changeFetchOptions({ method, body, headers }, url));
    const data = await res.json();
    return { res, data };
  }

  protected async update(entity: Partial<TData>): Promise<IResult<TData>> {
    const url = this.getDetailUrl(entity);
    const method = 'PUT';
    const body = JSON.stringify(this.onBeforeUpdate(entity));
    const headers = { 'content-type': 'application/json', accept: 'application/json' };
    const res = await this.fetch(url, this.changeFetchOptions({ method, body, headers }, url));
    const data = await res.json();
    return { res, data };
  }

  protected async delete(
    entityOrId: string | number | IIdentified,
    params?: any
  ): Promise<IResult<null>> {
    const method = 'DELETE';
    const url = this.getDetailUrl(entityOrId);
    const res = await this.fetch(url, this.changeFetchOptions({ method, params }, url));
    return { res, data: null };
  }

  protected onBeforeCreate(obj: Partial<TData>): Partial<TData> {
    delete obj.id;
    return obj;
  }

  protected onBeforeUpdate(objWithId: Partial<TData>): Partial<TData> {
    return objWithId;
  }

  protected changeFetchOptions(options: any, _url = '') {
    return options;
  }

  protected getUrl(url = ''): string {
    const baseUrl = this.baseUrl.replace(/\/+$/, '');
    const partialUrl = url.replace(/^\/+/, '');
    return `${baseUrl}/${partialUrl}`;
  }

  protected getEntityId(entityOrId: string | number | IIdentified): string {
    if (entityOrId === undefined || entityOrId === null) {
      return '';
    }

    if (typeof entityOrId === 'object') {
      return this.getEntityId(entityOrId.id);
    }

    return `${entityOrId}`;
  }

  protected getDetailUrl(entityOrId: string | number | IIdentified): string {
    const id = this.getEntityId(entityOrId);
    return `${this.listUrl}/${id}`;
  }

  protected async fetch(url: string, init?: IFetchOptions): Promise<IFetchResponse> {
    const fullUrl = this.getUrl(url);
    const res: IFetchResponse = await this.client.fetch(fullUrl, init);
    return await this.handleErrorStatusCodes(res);
  }

  /**
   * Checks the response for errors and throws exceptions, otherwise returns the response as is.
   *
   * @param response The response from server.
   *
   * @returns If no errors are detected, it returns the same response.
   *
   * @throws If an error is detected, it throws `{ res, data }`, where `data` contains error details from server.
   */
  protected async handleErrorStatusCodes(response: IFetchResponse): Promise<IFetchResponse> {
    if (response.status >= 400) {
      let data = null;
      try {
        data = await response.json();
      } catch (ex) {
        try {
          data = await response.text();
        } catch (ex) {
          // do nothing
        }
      }
      throw { res: response, data };
    }
    return response;
  }

  protected mimeType(type: string): string {
    return `application/vnd.com.nsn.cumulocity.${type}+json`;
  }

  protected getIdString(reference: number | string | IIdentified): string {
    let id;
    if (typeof reference === 'object') {
      id = reference.id;
    } else {
      id = reference;
    }
    return String(id);
  }

  protected getPaging(json: any, filter: object): Paging<TData> {
    if (json.statistics) {
      const statistics = {
        ...json.statistics,
        nextPage: this.getCurrentPageFromLink(json.next),
        prevPage: this.getCurrentPageFromLink(json.prev)
      };
      return new Paging<TData>(this, statistics, filter);
    }
    return null;
  }

  protected getCurrentPageFromLink(link = '') {
    const matches = link.match(/currentPage=(-{0,1}\d+)/);
    return matches && parseInt(matches[1], 10);
  }
}
