import { IFetchClient, Service, IResult, IFetchResponse } from '../core';
import { IApplication } from './IApplication';
import { IApplicationBinary } from './IApplicationBinary';
import { Stream } from 'stream';
import FormData from 'form-data';
import { IUploadParamsOverride } from './IUploadParamsOverride';

export class ApplicationBinaryService extends Service<IApplicationBinary> {
  protected baseUrl;
  protected listUrl = 'binaries';
  protected propertyName = 'attachments';

  constructor(client: IFetchClient, applicationOrId: string | number | IApplication) {
    super(client);
    this.baseUrl = `application/applications/${this.getIdString(applicationOrId)}`;
  }

  async upload(
    binary: ArrayBuffer | Stream | Buffer | File | Blob,
    fileName?: string,
    uploadParamsOverride?: IUploadParamsOverride
  ): Promise<IResult<IApplicationBinary>> {
    const url = uploadParamsOverride?.listUrl || this.listUrl;
    const method = 'POST';
    const body = this.createBinaryRequestBody(binary, fileName, uploadParamsOverride);
    let bodyHeaders;
    if (typeof body.getHeaders === 'function') {
      bodyHeaders = body.getHeaders();
    }
    const headers = Object.assign(
      uploadParamsOverride?.headers || {
        Accept: 'application/json'
      },
      bodyHeaders
    );
    const res = await this.fetch(url, { method, body, headers });
    const data = await res.json();
    return { res, data };
  }

  uploadWithProgressXhr(
    binary: ArrayBuffer | Stream | Buffer | File | Blob,
    onProgress,
    fileName?: string,
    uploadParamsOverride?: IUploadParamsOverride
  ): XMLHttpRequest {
    const url = `/${this.baseUrl}/${uploadParamsOverride?.listUrl || this.listUrl}`;
    const method = 'POST';
    const body = this.createBinaryRequestBody(binary, fileName, uploadParamsOverride);
    let bodyHeaders;
    if (typeof body.getHeaders === 'function') {
      bodyHeaders = body.getHeaders();
    }
    const headers = this.client.getFetchOptions().headers;
    Object.assign(
      headers,
      uploadParamsOverride?.headers || {
        Accept: 'application/json'
      }
    );
    Object.assign(headers, bodyHeaders);
    const xhr = new XMLHttpRequest();
    xhr.open(method, url, true);
    for (const key in headers) {
      if (headers.hasOwnProperty(key)) {
        xhr.setRequestHeader(key, headers[key]);
      }
    }

    xhr.upload.addEventListener('progress', onProgress);

    let xhrBody: XMLHttpRequestBodyInit;
    if (typeof body.getBuffer === 'function') {
      xhrBody = body.getBuffer();
    } else {
      xhrBody = body as any;
    }
    xhr.send(xhrBody);
    return xhr;
  }

  getXMLHttpResponse(xhr): Promise<IApplication> {
    return new Promise((res, rej) => {
      xhr.addEventListener('loadend', () => {
        xhr.readyState === 4 && (xhr.status === 200 || xhr.status === 201)
          ? res(JSON.parse(xhr.responseText))
          : rej(
              xhr.responseText ? { data: JSON.parse(xhr.responseText) } : 'Could not upload file.'
            );
      });
    });
  }

  async list(filter?) {
    return super.list(filter);
  }

  async delete(binaryOrId: string | number | IApplicationBinary) {
    return super.delete(binaryOrId);
  }

  async listPlugins() {
    const headers = { accept: 'application/json' };
    const url = `${this.listUrl}/plugins`;
    const res = await this.fetch(url, { headers });
    const data = await res.json();
    return { res, data };
  }

  async addPlugin(pluginName: string, pluginFile: Stream | Buffer | ArrayBuffer) {
    const url = `${this.listUrl}/plugins/${encodeURIComponent(pluginName)}`;
    const method = 'POST';
    const body = new FormData();
    const bufferOrStream = pluginFile instanceof ArrayBuffer ? Buffer.from(pluginFile) : pluginFile;
    body.append('file', bufferOrStream);

    const headers = {
      accept: 'application/json'
    };

    const res = await this.fetch(url, { method, body, headers });
    const data = await res.json();
    return { res, data };
  }

  async removePlugin(pluginName: string) {
    const method = 'DELETE';
    const headers = { accept: 'application/json' };
    const url = `${this.listUrl}/plugins/${pluginName}`;
    const res = await this.fetch(url, { method, headers });
    const data = await res.json();
    return { res, data };
  }

  async updateFiles(files: Array<{ path: string; contents: Stream | Buffer | ArrayBuffer }>) {
    const url = `${this.listUrl}/files`;
    const method = 'POST';
    const body = new FormData();
    files.forEach(file => {
      const bufferOrStream =
        file.contents instanceof ArrayBuffer ? Buffer.from(file.contents) : file.contents;
      body.append(file.path, bufferOrStream);
    });
    const headers = {
      accept: 'application/json'
    };
    const res = await this.fetch(url, { method, body, headers });
    const data = await res.json();
    return { res, data };
  }

  async downloadArchive(binaryId: number | string): Promise<IFetchResponse> {
    const url = `${this.listUrl}/${binaryId}`;
    return await this.fetch(url);
  }

  private createBinaryRequestBody(
    binary: ArrayBuffer | Stream | Buffer | File | Blob,
    fileName?: string,
    uploadParamsOverride?: IUploadParamsOverride
  ) {
    const body = new FormData();
    const bufferOrStream = binary instanceof ArrayBuffer ? Buffer.from(binary) : binary;
    let uploadFileName = fileName;
    if (typeof File !== 'undefined' && binary instanceof File) {
      uploadFileName = binary.name;
    }
    body.append(uploadParamsOverride?.bodyFileProperty || 'file', bufferOrStream, uploadFileName);
    body.append('fileName', uploadFileName);
    if (uploadParamsOverride?.requestBody) {
      for (const key in uploadParamsOverride.requestBody) {
        if (uploadParamsOverride.requestBody.hasOwnProperty(key)) {
          body.append(key, JSON.stringify(uploadParamsOverride.requestBody[key]));
        }
      }
    }
    return body;
  }
}
