import { btoa } from 'b2a';
import { IIdentified, Service, IResult, IResultList } from '../core';
import {
  IDeviceRegistration,
  IDeviceRegistrationAccept,
  IDeviceRegistrationCreate,
  IDeviceRegistrationLimit
} from './IDeviceRegistration';
import { DeviceRegistrationStatus } from './DeviceRegistrationStatus';
import { IDeviceCredentials } from './IDeviceCredentials';

/**
 * Interface to use as options parameter
 * to bootstrap a new device.
 */
export interface IDeviceBootstrapOptions {
  /**
   * If already logged in it is accessible via
   * client.auth.getFetchOptions, see [[BasicAuth.getFetchOptions]]
   *
   * **Example**
   * ``` typescript
   *
   * // tenant/username:password
   * const basicAuthToken = 'Basic dGVuYW50L3VzZXJuYW1lOnBhc3N3b3Jk';
   * ```
   */
  basicAuthToken?: string;
  /**
   * Object to define username and password
   * as string.
   *
   * **Example**
   * ``` typescript
   *
   *    const basicAuth = {
   *      user: 'username',
   *      pass: 'password'
   *    }
   * ```
   */
  basicAuth?: {
    user: string;
    pass: string;
  };
}

/**
 * This class allows registration of a new device.
 */
export class DeviceRegistrationService extends Service<IDeviceRegistration> {
  protected baseUrl = 'devicecontrol';
  protected listUrl = 'newDeviceRequests';
  protected propertyName = 'newDeviceRequests';

  /**
   * Gets the details of device registration.
   *
   * @param {string|number|IIdentified} entityOrId Entity or Id of the entity.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *
   *    const entityId: number = 1;
   *
   *    (async () => {
   *      const {data, res} = await deviceRegistrationService.detail(entityId);
   *    })();
   * ```
   */
  async detail(entityOrId: string | number | IIdentified) {
    return super.detail(entityOrId);
  }

  /**
   * Creates a new device registration.
   *
   * @param {IDeviceRegistrationCreate} entity Device registration object with mandantory fragments.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *
   *    const mandantoryObject: IDeviceRegistrationCreate = {
   *      id: 1,
   *    };
   *
   *    (async () => {
   *      const {data, res} = await deviceRegistrationService.create(mandantoryObject);
   *    })();
   * ```
   */
  async create(entity: IDeviceRegistrationCreate) {
    return super.create(entity);
  }

  /**
   * Gets the list of device registrations by parameters.
   *
   * @returns Response wrapped in [[IResultList]]
   *
   * @param {object} filter Object containing filters for querying registrations.
   *
   * **Example**
   * ```typescript
   *
   *   const filter: object = {
   *     pageSize: 100,
   *     withTotalPages: true
   *   };
   *
   *   (async () => {
   *     const {data, res, paging} = await deviceRegistrationService.list(filter);
   *   })();
   * ```
   */
  async list(filter: object = {}) {
    return super.list(filter);
  }

  /**
   * Removes an registration with given id.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * @param {string | number | IIdentified} entityOrId entity or id of the registration.
   *
   * **Example**
   * ```typescript
   *
   *   const id = 'abc';
   *
   *   (async () => {
   *     const {data, res} = await deviceRegistrationService.delete(id);
   *     // data will be null
   *   })();
   * ```
   */
  async delete(entityOrId: string | number | IIdentified) {
    return super.delete(entityOrId);
  }

  /**
   * Accepts the device registration for given id.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * @param {string | number | IIdentified} entityOrId entity or id of registration.
   *
   * **Example**
   * ```typescript
   *
   *   const id = 'abc';
   *
   *   (async () => {
   *     const {data, res} = await deviceRegistrationService.accept(id);
   *   })();
   * ```
   */
  async accept(entityOrId: string | number | IIdentified): Promise<IResult<IDeviceRegistration>> {
    const securityToken: string = this.getSecurityToken(entityOrId);
    let payload: Partial<IDeviceRegistration> = {
      id: this.getIdString(entityOrId),
      status: DeviceRegistrationStatus.ACCEPTED
    };

    if (securityToken) {
      payload = {
        ...payload,
        securityToken
      };
    }
    return super.update(payload);
  }

  /**
   * Accepts the device registration for all registrations in pending acceptance.
   *
   * @returns Response wrapped in [[IResultList]]
   *
   * **Example**
   * ```typescript
   *
   *   (async () => {
   *     const {data, res} = await deviceRegistrationService.acceptAll();
   *   })();
   * ```
   */
  async acceptAll(): Promise<IResultList<IDeviceRegistrationAccept>> {
    const res = await super.fetch(`${this.listUrl}/acceptAll`, { method: 'POST' });
    const data = (await res.json()) as IDeviceRegistrationAccept[];
    return { res, data };
  }

  /**
   * Gets limitation information for registering devices to the platform
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *
   *   (async () => {
   *     const {data, res} = await deviceRegistrationService.limit();
   *   })();
   * ```
   */
  async limit(): Promise<IResult<IDeviceRegistrationLimit>> {
    const res = await super.fetch(`${this.listUrl}/limit`);
    const data = (await res.json()) as IDeviceRegistrationLimit;
    return { res, data };
  }

  /**
   * Bootstraps the device with given id.
   *
   * @param entityOrId entity or id of registration.
   * @param options for details see [[IDeviceBootstrapOptions]]
   *
   * **Example**
   * ```typescript
   *
   *   const id: 'abc';
   *   const options: IDeviceBootstrapOptions = {
   *     basicAuthToken: 'Basic dGVuYW50L3VzZXJuYW1lOnBhc3N3b3Jk',
   *     basicAuth: {
   *       user: 'username',
   *       pass: 'password'
   *     }
   *   };
   *
   *   (async () => {
   *     const {data, res} = await deviceRegistrationService.bootstrap(id, options);
   *   })();
   * ```
   */
  async bootstrap(
    entityOrId: string | number | IIdentified,
    options: IDeviceBootstrapOptions
  ): Promise<IResult<IDeviceCredentials>> {
    const id = this.getIdString(entityOrId);
    const securityToken = this.getSecurityToken(entityOrId);
    const body = securityToken ? JSON.stringify({ id, securityToken }) : JSON.stringify({ id });
    const url = `${this.baseUrl}/deviceCredentials`;
    const { basicAuth } = options;
    let { basicAuthToken } = options;
    if (basicAuth) {
      const { user, pass } = basicAuth;
      basicAuthToken = btoa(`${user}:${pass}`);
    }
    const headers = {
      Authorization: `Basic ${basicAuthToken}`,
      accept: 'application/json',
      'content-type': 'application/json'
    };
    const method = 'POST';
    const res = await this.client.fetch(url, { body, headers, method });
    const data = await res.json();
    if (res.status > 400) {
      throw { res, data };
    }
    return { res, data };
  }

  protected onBeforeCreate(entity: IDeviceRegistrationCreate): IDeviceRegistrationCreate {
    return entity;
  }

  protected onBeforeUpdate(entity: IDeviceRegistrationCreate): IDeviceRegistrationCreate {
    const noIdEntity = Object.assign({}, entity);
    delete noIdEntity.id;
    return noIdEntity;
  }

  protected getDetailUrl(entityOrId: string | number | IIdentified): string {
    const id = encodeURIComponent(this.getEntityId(entityOrId));
    return `${this.listUrl}/${id}`;
  }

  private getSecurityToken(entity: string | number | IIdentified): string {
    let securityToken: string;
    if (typeof entity === 'object') {
      securityToken = entity.securityToken ? String(entity.securityToken) : undefined;
    }
    return securityToken;
  }
}
